import React from 'react'
import Announcement from '../components/Announcement'
import Footer from '../components/Footer'
import Navbar from '../components/Navbar'
import NewsLetter from '../components/NewsLetter'
import styled from 'styled-components'
import { useSelector } from 'react-redux'
const Container = styled.div``;
const Wrapper = styled.div`
`
const Title = styled.div`
font-size: 30px;
font-size:300;
display: flex;
justify-content: center;
align-items: center;
padding: 20px 0px;

@media (max-width: 500px){
    flex-direction: column;   
   }
`
const Topbar = styled.div`
display: flex;
justify-content: space-between;
align-items: center;
padding: 10px 10px;
`
const Left = styled.div`

`
const Button1 = styled.button`
padding: 10px;
background: transparent;
`
const Center = styled.div`
display: flex;
font-size: 18px;
font-weight: 300;
padding: 0px 10px;

@media (max-width: 600px){
    display: none;   
   }
`
const Para1 = styled.div`
padding: 0px 10px;
text-decoration: underline;
`
const Right = styled.div``
const Button2 = styled.div`
padding: 10px;
background: black;
color: white;
`
const Bottombar = styled.div`
display: flex;
@media (max-width: 600px){
    display: flex;
    flex-direction: column;  
   }
`
const InfoContainer = styled.div`
flex: 2;
padding-right: 100px;
@media (max-width: 600px){
    flex-direction: column;   
    padding-right: 0px;
  }
`;
const Info = styled.div`
display: flex;
justify-content: space-between;
align-items: center;

`;
const Left1 = styled.div`
display: flex;
justify-content: space-between;
align-items: center;
font-size: 20px;
@media (max-width: 600px){
    flex-direction: column;   
  }
`;
const ImageContainer = styled.div``;
const Image = styled.img`
object-fit: contain;
width: 200px;
height: 200px;
@media (max-width: 600px){
    width: 250px;
    height: 250px;   
  }
`;
const ProductDetail = styled.div``;
const Title1 = styled.div``;
const Id = styled.div``;
const Size = styled.div``;
const Right1 = styled.div`
display: flex;
flex-direction: column;

`;
const QuantityContainer = styled.div`
display: flex;
justify-content: center;
align-items: center;
`;
const Remove = styled.div`
font-size: 30px;
@media (max-width: 600px){
    font-size: 20px;   
  }
`;
const Quantity = styled.div`
font-size: 30px;
padding-right: 10px;

@media (max-width: 600px){
    font-size: 20px;   
  }
`;
const Add = styled.div`
font-size: 30px;
padding-right: 10px;
@media (max-width: 600px){
    font-size: 20px;   
  }
`;
const Price = styled.div`
font-size: 30px;
letter-spacing: 2px;
@media (max-width: 600px){
    font-size: 20px;   
  }
`;

const SumaryContainer= styled.div`
flex: 1;
margin-top: 20px;
`;
const Sumary = styled.div`
flex: 1;
border: 1px solid grey; 
border-radius: 15px;
padding: 20px 0px;
font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
`;
const SummaryTitle = styled.div`
padding: 20px 0px 10px 10px;
font-size: 35px;
font-weight 100;

@media (max-width: 768px){
    font-size: 24px;   
   }
`;
const SummaryContainer = styled.div`
display: flex;
justify-content: space-between;
align-items: center;
padding: 0px 10px;
`;
const SummaryLeft = styled.div`
font-size: 18px;
padding: 10px 0px 20px 10px;

@media (max-width: 768px){
    font-size: 16px;   
   }
`;
const SummaryRight = styled.div`
font-size: 18px;
padding-bottom: 20px;

@media (max-width: 768px){
    font-size: 18px;   
   }
`;
const Button = styled.button`
width: 100%;
height: 40px;
background: black;
color: white;
cursor: pointer;
`

const Cart = () => {
    const cart = useSelector(state=>state.cart);
    //  console.log(cart);
  return (
    <Container>
     <Announcement/>
     <Navbar/>
     <Wrapper>
        <Title>YOUR BAG</Title>
        <Topbar>
            <Left>
                <Button1>CONTINUE SHOPPING</Button1>
            </Left>
            <Center>
                <Para1>Shopping Bag(2)</Para1>
                <Para1>Your Wishlist(2)</Para1>
            </Center>
            <Right>
           <Button2>CHECKOUT NOW</Button2>
            </Right>
        </Topbar>
        <Bottombar>

            <InfoContainer>
                {cart.products.map((product)=>(
   
                    <Info key={product.id} >
            <Left1>

                    <ImageContainer>
                <Image src={product.img} />
            </ImageContainer>
            <ProductDetail>
                <Title1><b>Product:</b> Product Title</Title1>
                <Id><b>ID:</b> {product.id}</Id>
                <Size><b>Size:</b> {product.size}</Size>
            </ProductDetail>
            </Left1>
            <Right1>
                <QuantityContainer>              
                <Add>+</Add>
                <Quantity>{cart.quantity}</Quantity>
                <Remove>-</Remove>
                </QuantityContainer>
                <Price>${product.price}</Price>
            </Right1>
        </Info>
                ))}
        </InfoContainer>
     <SumaryContainer>
     <Sumary>
        <SummaryTitle>ORDER SUMMARY</SummaryTitle>
        <SummaryContainer>
            <SummaryLeft>Subtotal</SummaryLeft>
            <SummaryRight>${cart.total}</SummaryRight>
        </SummaryContainer>
        <SummaryContainer>
            <SummaryLeft>Estimated Shipping</SummaryLeft>
            <SummaryRight>$5.56</SummaryRight>
        </SummaryContainer><SummaryContainer>
            <SummaryLeft>Shipping Discount</SummaryLeft>
            <SummaryRight>$6.34</SummaryRight>
        </SummaryContainer>
        <SummaryContainer>
            <SummaryLeft style={{fontSize: '30px'}} ><b>Total</b></SummaryLeft>
            <SummaryRight style={{fontSize: '30px'}}><b>${cart.total}</b></SummaryRight>
        </SummaryContainer>
        <SummaryContainer>
            <Button>CHECKOUT NOW</Button>
        </SummaryContainer>
     </Sumary>
     </SumaryContainer>
</Bottombar>
     </Wrapper>
     <NewsLetter/>
     <Footer/>
    </Container>
  )
}

export default Cart