import React from 'react'
import styled from 'styled-components'
// import { Link } from 'react-router-dom';

const Container = styled.div`
width: 98vw;
height: 100vh;
background: linear-gradient(rgba(255,255,255,0.5),rgba(255,255,255,0.5)), url("https://images.pexels.com/photos/6984650/pexels-photo-6984650.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940") center;
background-size: cover;
display: flex;
align-items: center;
justify-content: center;

`;

const Wrapper = styled.div`
width: 30%;
padding: 20px;
background-color: white;
@media (max-width: 768px){
  width: 50%;   
}
@media (max-width: 500px){
  width: 75%;   
}
`;
const Title = styled.h1`
font-size: 24px;
font-weight: 400;
`;

const Form = styled.form`
display: flex;
flex-direction: column;
`;

const Input = styled.input`
flex: 1;
min-width: 40%;
margin: 10px 0px ;
padding: 10px;
`;



const Button = styled.button`
width: 40%;
border:none;
padding: 15px 20px;
background-color: teal;
color: white;
cursor:pointer;
margin-bottom: 10px;
&:disabled{
  color: green;
  cursor:not-allowed;
} 
`;
const Link = styled.a`
margin: 5px 0px;
font-size: 12px;
text-decoration: underline;
cursor: pointer;
color: black;
`;

// const Error = styled.span`
// color: red;
// `;
const Login = () => {
//   const [username, setUserName] = useState("")
//   const [password, setPassword] = useState("")
//    const dispatch = useDispatch();
//    const {isFetching, error} = useSelector((state) => state.user);
  const handleClick = (e) =>{
    e.preventDefault();
    // login(dispatch, {username, password});
  }
  return (
    <Container>
    <Wrapper>
     <Title>SIGN IN</Title>
     <Form>
         <Input placeholder='username' />
         <Input placeholder='password' type="password"  />
    
      <Button onClick={handleClick} >LOGIN</Button>
      {/* { error && <Error> Something went wrong...</Error>} */}
      <Link href="/register" >DO NOT YOU REMEMBER THE PASSWORD?</Link>
      <Link href="/register">CREATE A NEW ACCOUNT</Link>

     </Form>
    </Wrapper>
 </Container>
  )
}

export default Login