import React from 'react'
import styled from 'styled-components'

const Container = styled.div`
width: 98vw;
height: 100vh;
background: linear-gradient(rgba(255,255,255,0.5),rgba(255,255,255,0.5)), url("https://images.pexels.com/photos/6984661/pexels-photo-6984661.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940") center;
background-size: cover;
display: flex;
justify-content: center;
align-items: center;
`
const Wrapper = styled.div`
width: 40%;
padding: 20px;
background-color: white;
@media (max-width: 600px){
  width: 70%;   
}

`
const InputTitle = styled.h2`
font-size: 24px;
font-weight: 400;
font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif ;
`
const Form = styled.form`
display: flex;
flex-wrap:wrap;
`
const Input = styled.input`
flex: 1;
min-width: 40%;
margin: 12px 10px 0 0 ;
padding: 10px;
@media (max-width: 600px){
  margin-bottom: 15px;   
}
`;
const Agreement = styled.div`
font-size: 13px;
padding-top: 10px;
@media (max-width: 600px){
  margin-bottom: 10px;  
}
`
const Button = styled.button`
margin-top: 10px;
width: 40%;
padding: 10px 20px;
background: #07b2b2;
color: white;
cursor: pointer;
border: none;
@media (max-width: 600px){
  width:100%;   
}
`

const Register = () => {
  return (
    <Container>
        <Wrapper>
          <InputTitle>CREATE AN ACCOUNT</InputTitle>
          <Form>
            <Input placeholder='name' />
            <Input placeholder="last name" />
            <Input placeholder="username" />
            <Input placeholder="email"/>
            <Input placeholder="password" />
            <Input placeholder="confirm password"/>
             <Agreement>By creating an account, I consent to the processing of my personal data in accordance with the <b>PRIVACY POLICY</b></Agreement>
             <Button>CREATE</Button>
          </Form>
        </Wrapper>
    </Container>
  )
}

export default Register