import { FavoriteBorderOutlined, SearchOutlined, ShoppingCartOutlined } from '@material-ui/icons';
import React from 'react'
import { Link } from 'react-router-dom';
import styled from 'styled-components'
const IconContainer = styled.div`
opacity: 0;
width: 100%;
height: 100%;
position:absolute;
top: 0;
left: 0;
z-index:3;
background: rgba(0,0,0,0.2);
display: flex;
justify-content: center;
align-items: center;
transition: all 0.5s ease;
cursor: pointer;
`;
const Wrapper = styled.div`
min-width: 300px;
height: 350px;
flex:1;
margin: 10px;
background-color: #f5fbfd;
display: flex;
justify-content: center;
align-items: center;
position: relative;
&:hover ${IconContainer}{
    opacity: 1;
}

`;
const Over = styled.div`
width: 200px;
height: 200px;
border-radius: 50%;
background-color: #fff;
position: absolute;
`;

const Image = styled.img`
z-index:2;
height: 75%;

`;
const Icon = styled.div`
width: 40px;
height: 40px;
border-radius: 50%;
background-color: white;
display: flex;
align-items: center;
justify-content: center;
margin: 10px;
transition: all 0.5s ease;

&:hover{
    background-color: #e9f5f5;
    transform: scale(1.1);
}
`;

const Product = ({item}) => {
  return (
    <Wrapper>
        <Over/>
        <Image src={item.img}/>
        <IconContainer>
          <Link to="/cart">
        <Icon>
            <ShoppingCartOutlined/>
            </Icon> 
          </Link>
            <Link to={`product/${item.id}`}>
            <Icon>
            <SearchOutlined/>
            </Icon> 
            </Link>
            <Icon>
            <FavoriteBorderOutlined/>
            </Icon> 
        </IconContainer>
    </Wrapper>
  )
}

export default Product