import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
// import Product from '../components/Product'
import { Navigate, useLocation, useNavigate } from 'react-router-dom'
import { popularProducts } from '../data'
import Announcement from '../components/Announcement'
import Navbar from '../components/Navbar'
import NewsLetter from '../components/NewsLetter'
import Footer from '../components/Footer'
import { useDispatch } from 'react-redux'
import { addProduct } from '../redux/cartRedux'
const Container = styled.div`
flex:2;
margin: 0;
padding: 0;
`
const Wrapper = styled.div`
display: flex;
justify-content: center;
align-items: center;


@media (max-width: 500px){
    flex-direction: column;  
   }
`
const Left = styled.div`
flex:1;
display: flex;
justify-content: flex-start;
align-items: flex-start;
`
const ImageContainer = styled.div`
`
const Img = styled.img`
object-fit: contain;
width: 500px;
height: 400px;
@media (max-width: 768px){
    width: 400px;
    height: 300px;
}

@media (max-width: 500px){
    width: 300px;
    height: 200px;   
   }
`
const Right = styled.div`
flex:1;
display: flex;
align-items: flex-start;
justify-content: flex-start;
flex-direction: column;

`
const ProductTitle = styled.p`
font-size: 30px;
font-weight: 500;
padding: 0;

@media (max-width: 500px){
 font-size: 24px;   
}
`
const ProductDesc = styled.p`
font-size: 20px;
font-weight: 500;
padding-top: 0px;
margin-top: 0;
padding-bottom: 20px;
@media (max-width: 500px){
 font-size: 18px;   
}
`
const Price = styled.div`
font-size: 40px;
font-weight: 400;
padding-bottom: 20px;
letter-spacing: 2px;
@media (max-width: 500px){
 font-size: 30px;   
}
`;
const FilterContainer = styled.div`
display: flex;
justify-content: space-between;
align-items: center;
font-size: 20px;
font-weight: 400;
padding-bottom: 20px;
@media (max-width: 500px){
}
`
const FilterColor = styled.div`
width: 30px;
height: 30px;
border-radius: 50%;
margin: 0px 85px 0px 10px;
background: ${props=>props.color};
cursor: pointer;
@media (max-width: 500px){
    margin: 0px 30px 0px 10px;
}
`
const FilterSize = styled.div`
font-size: 20px;
font-weight: 400;
`
const Select = styled.select`
padding: 10px 20px;
margin-left: 10px;

`
const Option = styled.option``
const BottomContainer = styled.div`
display: flex;
justify-content: flex-start;
align-items: center;


@media (max-width: 500px){   
   }
`
const QuantityContainer = styled.div`
display: flex;
justify-content: center;
align-items: center;
padding-right: 70px;
@media (max-width: 500px){
 padding-right: 50px; 
}
`

const Remove = styled.div`
font-size: 30px;
font-weight: 600;
padding-right: 20px;
cursor: pointer;

`
const Quantity = styled.h4`
width: 50px;
height: 50px;
display: flex;
justify-content:center;
align-items: center;
border: 1px solid #07b2b2;
border-radius: 20px;


`
const Add = styled.div`
font-size: 30px;
font-weight: 600;
padding-left: 20px;
cursor: pointer;
`

const Button = styled.button`
padding: 15px;
border: 1px solid #07b2b2;
background: transparent;
&:hover{
    background: lightgrey;
}

`

const Product = () => {
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const location  = useLocation();
    const id = location.pathname.split("/")[2];
    const [product, setProduct] = useState([]);
    const [quantity, setQuantity] = useState(1);
    const [size, setSize] = useState("");
    const [color, setColor] = useState("");
    const price = "500";
    useEffect(() => {
        setProduct(popularProducts.filter((item)=> (id == item.id)))
    }, []);
    const handleQuantity = (type)=>{
        if(type === "inc"){
          setQuantity( quantity+1 )
        }else{
          quantity > 1 && setQuantity(quantity - 1)
        }
    }
// console.log(product);
    const handleClick = ()=>{
        // const productObject = Object.assign({}, ...[product]);
        
        dispatch
        (addProduct({...product[0], size, quantity,color, price})
        );
        console.log({...product[0], size, quantity, color,price});
        console.log(product);
  }
  return (
      <Container>
        <Announcement/>
        <Navbar/>
     <Wrapper>
        {product.map((item)=>(
            <Left>
            <ImageContainer  >
                <Img src={item.img}/>
            </ImageContainer>
        </Left>
            ))}
        <Right>
            <ProductTitle>Product</ProductTitle>
            <ProductDesc>
        Lorem ipsum dolor sit amet, consectetur adipisicing elit. Est voluptatum consectetur cupiditate at ex, explicabo aliquam pariatur quod beatae hic laudantium debitis suscipit dolorum excepturi tempora iste modi. Soluta, laborum?
            </ProductDesc>
            <Price>$500</Price>
            <FilterContainer>
                Color: 
                <FilterColor color={"aqua"} key={"aqua"} onClick={(e)=> setColor("aqua")}  />
                <FilterSize>Size: 
                    <Select name="size" onChange={(e)=> setSize(e.target.value)} >
                        <Option value="XS">XS</Option>
                        <Option value="S">S</Option>
                        <Option value="M">M</Option>
                        <Option value="L">L</Option>
                        <Option value="XL">XL</Option>

                    </Select>
                </FilterSize>
            </FilterContainer>
            <BottomContainer>
                <QuantityContainer>
             <Remove onClick={()=>handleQuantity("dec")} >-</Remove>
            <Quantity>{quantity}</Quantity>
            <Add onClick={()=>handleQuantity("inc")} >+</Add>
                </QuantityContainer>
            <Button onClick={handleClick} >ADD TO CART</Button>
            </BottomContainer>
        </Right>
     </Wrapper>
     <NewsLetter/>
     <Footer/>
    </Container>
  )
}

export default Product