import React, { useState } from 'react'
import styled from "styled-components";
import Announcement from '../components/Announcement';
import Navbar from '../components/Navbar';
import NewsLetter from '../components/NewsLetter';
import { useLocation } from 'react-router-dom';
import Products from '../components/Products';
import Footer from '../components/Footer';

const Container = styled.div``;
const Wrapper = styled.div``;
const Title = styled.h2`
font-size: 30px;
font-weight: 600;
padding: 0px 10px;

`;
const FilterContainer = styled.div`
display: flex;
justify-content: space-between;
align-items: center;
margin-bottom: 40px;

@media (max-width: 500px){
 flex-direction: column;   
}


`;
const FilterProduct = styled.div`
font-size: 20px;
font-weight: 500;
padding: 0px 10px;
`;
const SelectColor = styled.select`
padding: 10px 20px;
margin:0px  20px;
@media (max-width: 500px){
 padding: 10px 10px;
}
`;
const SelectSize = styled.select`
padding: 10px 20px;
@media (max-width: 500px){
 padding: 10px 10px;   
}
`;
const SortProduct = styled.div`
font-size: 20px;
font-weight: 500;
`;
const SelectSort = styled.select`
padding: 10px 20px;
margin:0px  20px;
@media (max-width: 500px){
padding: 10px 10px;   
margin: 10px 0px;
}

`;
const Option = styled.option``;



const ProductList = () => {
const Location = useLocation();
const cat = Location.pathname.split("/")[2];
const [filter, setFilter] = useState({});
const [sort, setSort] = useState("Newest");
const handleChange = (e) =>{
    const value = e.target.value;
    setFilter({
        ...filter,
        [e.target.name]: value,
    })
    
    
    
    
}
console.log(filter,sort);

  return (
    <>
    <Announcement/>
    <Navbar/>
    <Container>
        <Wrapper>
            <Title>{cat}</Title>
            <FilterContainer>
                <FilterProduct> Filter Product: 
                    <SelectColor onChange={handleChange} name='color'  >
                        <Option value="white" >White</Option>
                        <Option value="blue">Blue</Option>
                        <Option value="green">Green</Option>
                        <Option value="yellow">Yellow</Option>
                        <Option value="black">Black</Option>
                    </SelectColor>
                    <SelectSize onChange={handleChange} name='size' >
                        <Option value="XS">XS</Option>
                        <Option value="S">S</Option>
                        <Option value="M">M</Option>
                        <Option value="L">L</Option>
                        <Option value="XL">XL</Option>
                    </SelectSize>
                </FilterProduct>
                <SortProduct >Sort Products: 
                    <SelectSort  name="sort" onChange={(e)=>setSort(e.target.value)} >
                        
                    <Option value="newest">Newest</Option>
                    <Option value="asc" >Price(asc)</Option>
                    <Option value="desc">Price(desc)</Option>
                    </SelectSort>
                </SortProduct>
            </FilterContainer>
        </Wrapper>
        <Products/>
    </Container>
    <NewsLetter/>
    <Footer/>
    </>
  )
}

export default ProductList